<!--  -->
<template>
  <div v-if="data">
    <el-form-item label="">
      <el-select v-model="data.options.type">
        <el-option value="text" label="" />
        <el-option value="textarea" label="" />
        <el-option value="number" label="" />
        <el-option value="password" label="" />
        <el-option value="tel" label="" />
      </el-select>
    </el-form-item>

    <el-form-item label="">
      <el-input v-model="data.options.placeholder" placeholder="placeholder"/>
    </el-form-item>

    <el-form-item label="">
      <el-input v-model="data.options.defaultValue"/>
    </el-form-item>

    <el-form-item label="">
      <el-input v-model.number="data.options.maxlength" />
    </el-form-item>

    <el-form-item label="">
      <el-input v-model.number="data.options.minlength" />
    </el-form-item>

    <el-form-item label="">
      <el-input-number v-model.number="data.options.max" />
    </el-form-item>

    <el-form-item label="">
      <el-input-number v-model.number="data.options.min" />
    </el-form-item>

    <el-form-item label="">
      <el-input-number v-model.number="data.options.step" :min="0" />
    </el-form-item>

    <el-form-item label="">
      <el-input-number v-model="data.options.rows" :min="0" />
    </el-form-item>

    <template>
      <el-form-item
        label=""
        v-if="
          hasKey('rules') ||
          hasKey('readonly') ||
          hasKey('disabled') ||
          hasKey('allowClear')
        "
      >
        <el-checkbox
          v-if="hasKey('rules')"
          v-model="data.options.rules.required"
          ></el-checkbox
        >
        <el-checkbox v-if="hasKey('readonly')" v-model="data.options.readonly"
          ></el-checkbox
        >
        <el-checkbox v-if="hasKey('disabled')" v-model="data.options.disabled"
          ></el-checkbox
        >
        <el-checkbox v-if="hasKey('clearable')" v-model="data.options.clearable"
          ></el-checkbox
        >
      </el-form-item>
      <el-alert
        title=" https://element.eleme.cn/#/zh-CN/component/select"
        type="success"
        effect="dark">
      </el-alert>
  
    </template>
  </div>
</template>
  
  <script>
  import SvgIcon from '@/components/SvgIcon.vue'
  
  export default {
    name: 'inputConfig',
    components: {
      SvgIcon
    },
    props: {
      select: {
        type: Object
      }
    },
    emits: ['update:select'],
    data(){
      return {
        data:undefined
      }
    },
    watch:{
      data:{
        deep:true,
        handler(val){
          this.$emit('update:select',val)
        },
      },
      select:{
        deep:true,
        handler(val){
          this.data = val;
        },
      },
    },
    mounted(){
      this.data = this.$props.select;
    },
    methods:{
      hasKey(key){
        return Object.keys(this.data.options).includes(key)
      },
    }
  }
  </script>
  
  <style lang="less" scoped>
  .el-form-item{
    margin-bottom: 10px !important;
  }
  ul{
    list-style: none;
    padding: 0;
    margin: 0;
    .drag-li{
      display: flex;
      align-items: center;
      margin-bottom: 5px;
      font-size: 25px;
    }
  }
  .drag-item{
    font-size:24px;
    margin:0 5px;
    cursor:move;
  }
          
  
  </style>
  