<template>
  <div class="form-config-container">
    <el-form label-position="right" label-width="100px" size="small">
      <el-form-item label="">
        <el-radio-group v-model="data.labelPosition">
          <el-radio-button label="left"></el-radio-button>
          <el-radio-button label="right"></el-radio-button>
          <el-radio-button label="top"></el-radio-button>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="">
        <el-input type="number" v-model="data.labelWidth" :min="0" />
      </el-form-item>

      <el-form-item label="">
        <el-radio-group v-model="data.size">
          <el-radio-button label="large"></el-radio-button>
          <el-radio-button label="default"></el-radio-button>
          <el-radio-button label="small"></el-radio-button>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="data.hideRequiredAsterisk" />
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
  name: 'ElFormConfig',
  props: {
    config: {
      type: Object,
      required: true
    }
  },
  emits: ['update:config'],
  data(){
    return {
      data:{}
    }
  },
  watch:{
    data(val){
      this.$emit('update:config',val)
    },
    config(val){
      this.data = val;
    },
  },
  mounted(){
    this.data = this.$props.config;
  }
}
</script>

<style scoped>
.el-form-item{
  margin-bottom: 10px !important;
}
</style>
